.nav__list {
  list-style: none;
  display: flex;
  gap: 20px;
  padding: 0;
  height: 20px;
  justify-content: flex-start;
  width: 100%;
  margin: 10px 60px;
}

.nav__list li:last-of-type {
  margin-left: auto;
}

@media screen and (max-width: 720px) {
  .nav__list {
    display: none;
  }
}
