.header {
  width: 100vw;
  background-color: var(--black);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  transition: top 0.4s ease;
  box-shadow: 0px 0px 0px 1px rgba(36, 35, 33, 0.2);
}
